




































































































































































































































































































.c-startCode-text {
  width: 200px !important;
}
.c-item {
  display: flex;
  flex-direction: row;
  border-bottom: solid 1px #cccccc;
  .c-item-index {
    width: 50px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
  }
  .c-item-avatar {
    width: 100px;
    cursor: pointer;
  }
  .c-name-text {
    width: 150px;
  }
  .c-item-rightcontent {
    flex: 1;
    padding: 5px;
    font-size: 14px;
    display: flex;
    flex-direction: column;
    ul li {
      margin-bottom: 10px;
    }
  }
  .c-btnbox {
    text-align: right;
    button {
      margin-left: 8px;
      font-size: 13px;
    }
  }
}
.c-topbox {
  padding: 5px;
  font-size: 14px;
  margin-bottom: 6px;
  border-bottom: solid 1px #cccccc;
  span {
    margin-right: 10px;
  }
}
