#app {
  font-family: "Microsoft YaHei", Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  max-width: 750px;
  margin: 0 auto;
}
html,
body,
#app {
  height: 100%;
}
ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}
.page {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.spacer {
  flex: 1;
}
nav {
  width: 100%;
  height: 40px;
  line-height: 40px;
  background: #2d8cf0;
  color: #FFFFFF;
  text-align: center;
  font-size: 20px;
  position: relative;
}
nav .left-side {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 15px;
}
nav .left-side span {
  cursor: pointer;
  margin-left: 8px;
}
nav .right-side {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 15px;
}
nav .right-side span {
  cursor: pointer;
  margin-right: 8px;
}
.content {
  flex: 1;
  min-height: 0;
  overflow: auto;
}
